import React from 'react';
import { MobileOnlyView } from 'react-device-detect';
import { ReactComponent as SVGProjects} from './portfolioProjects.svg';
import { ReactComponent as SVGScrollLeft} from './portfolioScrollLeft.svg';
import { ReactComponent as SVGScrollRight} from './portfolioScrollRight.svg';



class ProjectsView extends React.Component{
    constructor(props){
        super(props);

        this.state = {
        }
        this.currentCard = 1;
        this.totalCards = 2;
        this.slideLeft = this.slideLeft.bind(this);
        this.slideRight = this.slideRight.bind(this);
    }
    
    slideLeft(){
        if(this.currentCard > 1)
        {
            
            this.currentCard = (this.currentCard - 1);
           
            document.getElementById("projectTileswrapper").style.transform = "translateX(-" + ((this.currentCard-1)*260) + "px)";
            document.getElementById("slideCounter").innerHTML = this.currentCard + "/" + this.totalCards;
        }
    }

    slideRight(){
        if(this.currentCard < this.totalCards)
        {
            this.currentCard = (this.currentCard + 1);
            document.getElementById("projectTileswrapper").style.transform = "translateX(-" + ((this.currentCard-1)*260) + "px)";
            document.getElementById("slideCounter").innerHTML = this.currentCard + "/" + this.totalCards;
        }
    }
  
    render(){
        return(
            <div class="projectsPageCard" id="card3">
                <div class="svgWrapper">
                    <SVGProjects></SVGProjects>
                </div>
                <div class="projectsContainer">
                    <div id="projectTileswrapper">
                        <div class="projectTile">
                            <div class="projectPreview" id="eqCompanionPreview">  
                            </div>
                            <div class="projectTitle">
                                Equation Companion
                            </div>
                            <div class="projectDescriptionContainer">

                            </div>
                            <div class="projectLinks">
                                <button class="liveProjectButton">
                                    Live website
                                </button>
                                <button class="codeProjectButton">
                                    Code
                                </button>
                            </div>
                        </div>
                        <div class="projectTile">
                            <div class="projectPreview" id="comingSoonPreview">  
                            </div>
                            <div class="projectTitle">
                                *coming soon*
                            </div>
                            <div class="projectDescriptionContainer">

                            </div>
                            <div class="projectLinks">
                                
                            </div>
                        </div>
                        
                    </div>
                </div>
                <MobileOnlyView>
                <div class="sliderButtons">
                    <div class="slideLeft" onClick={this.slideLeft}><SVGScrollLeft></SVGScrollLeft></div>
                    <div id="slideCounter">1/2</div>
                    <div class="slideRight" onClick={this.slideRight}><SVGScrollRight></SVGScrollRight></div>
                </div>
                </MobileOnlyView>
            </div>
        );
    }
}
  
export default ProjectsView;